.rdy-solutions {
  padding-top: 124px;

  &__container {
    padding: 0;
  }

  &__title {
    margin-bottom: 74px;
    font-size: calc(34px + 12 * (100vw / 1920));

    @media (max-width: 1024px) {
      padding-left: 15px;
    }

    @media (max-width: 767px) {
      font-size: calc(34px + (12 + 12 * 0.7) * ((100vw - 320px) / 1920));
    }
  }
}
