//     sass- -  root
// colors
:root {
  // base
  --font-family: "Roboto", sans-serif;
  --content-width: 1190px;
  --content-fluid-width: 1586px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(content-fluid-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --dark-color: #2b2b2b;
  --black-color: #000;
  --science-blue: #014cdd;
  --manatee-color: #9097a6;
  --mercury-color: #e4e4e4;
  --torea-bay: #0d3397;
  --pomegranate-color: #f03a3a;
  --tundora-color: #444;
  --royal-blue: #547ce4;
  --catalina-blue: #0b327d;
}

