.about {
  padding-top: 41px;
  padding-bottom: 85px;

  &__container {
    display: grid;
    column-gap: 20px;
    align-items: center;
    grid-template-columns: 3fr 1fr;

    @media (max-width: 1600px) {
      grid-template-columns: 1fr;
      row-gap: 50px;
    }

    @media (max-width: 768px) {
      padding: 0;
    }
  }

  &__title {
    align-self: end;
    grid-column: 1 / span 1;
    margin-bottom: 20px;
    padding-right: 15px;
    padding-left: 15px;

    @media (max-width: 1600px) {
      grid-row: 1 / 1 span;
    }
  }

  &__descr {
    align-self: start;
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    padding-right: 15px;
    padding-left: 15px;
    font-family: "RobotoCondensed", sans-serif;
    font-weight: 300;
    font-size: calc(16px + 6 * (100vw / 1920));
    line-height: 1.45;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(16px + (6 + 6 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 1600px) {
      grid-row: 2 / 1 span;
    }
  }

  &__drum {
    justify-self: end;
    grid-column: 2 / span 1;
    grid-row: 1 / span 2;

    @media (max-width: 1024px) {
      display: none;
    }

    @media (max-width: 1600px) {
      justify-self: center;
      grid-column: 1 / span 2;
      grid-row: 3 / 1 span;
    }
  }

  &__accordion-wrapper {
    display: none;

    @media (max-width: 1024px) {
      display: block;
    }
  }
}

.fb-svg .fb-icon {
  fill: #0fe;
}
