.i-solutions {
  padding-top: 122px;
  padding-bottom: 121px;
  background: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, rgb(0 0 0 / 45%) 56.77%), url("../img/i-solutions.jpg") center / cover no-repeat;
  background-blend-mode: normal;

  &__container {
    flex-flow: column nowrap;
    align-items: center;
  }

  &__title {
    margin-bottom: 21px;
    font-family: "Noto", sans-serif;
    font-weight: 700;
    font-size: calc(34px + 26 * (100vw / 1920));
    line-height: 1;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(34px + (26 + 26 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__descr {
    margin-bottom: 30px;
    max-width: 1200px;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: calc(16px + 12 * (100vw / 1920));
    line-height: 1.15;
    text-align: center;
    color: var(--light-color);

    @media (max-width: 568px) {
      text-align: left;
    }

    @media (max-width: 767px) {
      font-size: calc(16px + (12 + 12 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__btn {
    padding-right: 42.5px;
    padding-left: 42.5px;

    &:hover {
      color: var(--dark-color);
      transition: color 0.2s ease-in-out;
    }

    @media (max-width: 568px) {
      display: flex;
      justify-content: center;
      width: 100%;
    }
  }

  @media (max-width: 568px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
