.contact-pm {
  &__container {
    position: relative;
    min-height: 650px;

    &--cases {
      position: relative;
    }
  }

  &__content {
    position: absolute;
    bottom: -200px;
    padding: 114px 0;
    background: url("../img/contact-bg.svg") bottom left / auto no-repeat, var(--science-blue);
    inset: 0;

    &--cases {
      position: absolute;
      bottom: -70px;
      background: var(--light-color);
    }

    @media (max-width: 1200px) {
      position: static;
      bottom: 0;
      padding: 0;
      padding-right: 15px;
      padding-bottom: 50px;
      padding-left: 15px;
    }
  }

  &__title {
    grid-column: 2 / span 5;
    margin-top: 57px;
    font-weight: 700;
    font-size: calc(34px + 41 * (100vw / 1920));
    line-height: 1.134;
    color: var(--light-color);

    &--cases {
      color: var(--science-blue);
    }

    @media (max-width: 767px) {
      margin-bottom: 30px;
      font-size: calc(34px + (41 + 41 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 1200px) {
      grid-column: 2 / 6 span;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 9 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__form {
    grid-column: 7 / span 5;

    @media (max-width: 1200px) {
      grid-column: 2 / 6 span;
      grid-row: 2 / span 1;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 9 span;
      grid-row: 2 / span 1;
    }
  }

  &--cases {
    position: relative;
  }
}
