.case-content {
  display: grid;
  grid-template-columns: 4fr 2fr;
  box-shadow: 0 40px 40px rgb(0 0 0 / 25%), 0 4px 40px rgb(0 0 0 / 25%);

  &__title {
    font-size: calc(34px + 2 * (100vw / 1920));

    @media (max-width: 767px) {
      font-size: calc(34px + (2 + 2 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  @media (max-width: 949px) {
    grid-template-columns: 1fr;
  }
}
