.main-btn {
  border: 1px solid var(--light-color);
  border-radius: 10px;
  padding: 20px 0;
  max-height: 60px;
  font-weight: 400;
  font-size: 18px;
  color: var(--light-color);
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out, color 0.2s ease-in-out;

  &:hover {
    background-color: var(--light-color);
    transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out, color 0.2s ease-in-out;
  }
}
