.nav {
  &__list {
    display: flex;
    align-items: center;

    @media (max-width: 390px) {
      justify-content: space-between;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 32px;

      @media (max-width: 390px) {
        margin-right: 0;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--light-color);
    transition: opacity 0.2s ease-in-out;

    &:hover {
      opacity: 0.6;
      transition: opacity 0.2s ease-in-out;
    }

    &--current {
      opacity: 0.6;
    }

    @media (max-width: 390px) {
      font-size: 16px;
    }
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
