.accordion {
  --accordion-time: 0.3s;
}

.content {
  margin: 0 auto;
  max-width: 1000px;
}

.accordion__control {
  display: flex;
  align-items: end;
  justify-content: space-between;
  border: none;
  padding: 10px;
  width: 100%;
  height: 132px;
  color: var(--light-color);
  background-color: transparent;
  cursor: pointer;

  &--1 {
    background: url("../img/case-1-img.jpg") center / cover no-repeat;
  }

  &--2 {
    background: url("../img/case-2-img.jpg") center / cover no-repeat;
  }

  &--3 {
    background: url("../img/case-3-img.jpg") center / cover no-repeat;
  }

  &--4 {
    background: url("../img/case-4-img.jpg") center / cover no-repeat;
  }

  &--5 {
    background: url("../img/case-5-img.jpg") center / cover no-repeat;
  }

  &--6 {
    background: url("../img/case-6-img.jpg") center / cover no-repeat;
  }
}

.accordion__content {
  overflow: hidden;
  padding: 0 20px;
  max-height: 0;
  box-sizing: content-box;
  background-color: var(--science-blue);
  opacity: 0;
  transition: all var(--accordion-time) ease-out;
  will-change: max-height;
}

.accordion__title {
  display: flex;
  align-items: center;
  font-family: "Noto Sans", sans-serif;
  font-size: calc(22px + 12 * (100vw / 1920));
  line-height: 1;
  text-decoration-line: underline;
  text-transform: uppercase;
  color: var(--light-color);

  @media (max-width: 767px) {
    font-size: calc(22px + (12 + 12 * 0.7) * ((100vw - 320px) / 1920));
  }
}

.accordion__icon {
  transition: transform var(--accordion-time) ease-out;
}

.is-open .accordion__icon {
  transform: rotate(180deg);
}

.is-open .accordion__content {
  padding: 20px;
  opacity: 1;
}

.is-open .accordion__control {
  background: none;
  background-color: var(--science-blue);
}
