.about-company {
  padding-top: 165px;
  padding-bottom: 179px;
  background: url("../img/about-company-bg.jpg") center / cover no-repeat;

  &__title {
    margin-bottom: 28px;
    font-family: "Noto", sans-serif;
    font-weight: 600;
    font-size: calc(34px + 11 * (100vw / 1920));
    line-height: 1;
    text-transform: uppercase;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(34px + (11 + 11 * 0.7) * ((100vw - 320px) / 1920));
      text-transform: none;
    }
  }

  &__descr {
    font-family: "RobotoCondensed", sans-serif;
    font-weight: 300;
    font-size: calc(20px + 2 * (100vw / 1920));
    line-height: 1.45;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(20px + (2 + 2 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__list {
    display: grid;
    column-gap: 85px;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 84px;

    @media (max-width: 1224px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    &--first {
      padding-top: 20px;
      padding-left: 6px;
    }
  }

  &__item:nth-child(2) {
    &::before {
      content: "01";
    }
  }

  &__item:nth-child(3) {
    &::before {
      content: "02";
    }
  }

  &__item:nth-child(4) {
    &::before {
      content: "03";
    }
  }

  @media (max-width: 1024px) {
    padding: 50px 0;
  }
}
