/* hero */
.hero {
  padding: 150px 0;
  background: url("../img/hero-bg.jpg") center / cover no-repeat;

  &__container {
    position: relative;
  }

  &__social-media {
    position: absolute;
    left: -15px;
    top: 55px;

    @media (max-width: 1024px) {
      display: none;
    }
  }

  &__slider-wrapper {
    grid-column: 3 / span 10;

    @media (max-width: 1024px) {
      grid-column: 1 / 12 span;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 9 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__small-txt {
    display: block;
    margin-bottom: 20px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.14;
    color: var(--light-color);
  }

  &__big-txt {
    font-family: "Noto", sans-serif;
    font-weight: 800;
    font-size: calc(34px + 20 * (100vw / 1920));
    line-height: 1.1;
    text-transform: uppercase;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(34px + (20 + 20 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__btns {
    @media (max-width: 768px) {
      display: none;
    }
  }

  &__nav {
    display: grid;
    column-gap: 68px;
    align-items: center;
    grid-column: 2 / span 11;
    grid-row: 2 / span 1;
    grid-template-columns: 8fr 1fr;
    background: none;

    @media (max-width: 768px) {
      grid-column: 1 / 9 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &--cases {
    position: relative;
    height: 822px;
    background: url("../img/cases-bg.jpg") center / cover no-repeat;
  }

  @media (max-width: 1024px) {
    padding-top: 20px;
    padding-bottom: 100px;
  }

  @media (max-width: 768px) {
    padding-top: 60px;
    height: auto;
  }
}

/* hero-social-media */
.hero-social-media {
  &__list {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    row-gap: 90px;
  }

  &__item {
    transform: rotate(-90deg);
  }

  &__link {
    display: flex;
    align-items: center;

    &:hover span {
      color: var(--light-color);
      transition: color 0.2s ease-in-out;
    }

    &:hover svg {
      fill: var(--light-color);
      transition: fill 0.2s ease-in-out;
    }
  }

  &__txt {
    display: inline-block;
    color: var(--manatee-color);
    transition: color 0.2s ease-in-out;
  }

  &__svg {
    display: inline-block;
    margin-left: 8px;
    width: 21px;
    height: 21px;
    fill: var(--manatee-color);
    transition: fill 0.2s ease-in-out;
  }

  @media (max-width: 768px) {
    display: none;
  }
}
