.social-media-svg {
  fill: var(--manatee-color);
  transition: fill 0.2s ease-in-out;

  &:hover {
    fill: var(--light-color);
  }

  &--white {
    fill: var(--light-color);
    transition: opacity 0.2s ease-in-out;

    &:hover {
      opacity: 0.5;
      transition: opacity 0.2s ease-in-out;
    }
  }
}
