.drum {
  position: relative;
  width: 606px;
  height: 606px;

  &__segment {
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;

    &--1 {
      left: 50px;
      top: 121px;
      transition: left 0.2s ease-in-out, top 0.2s ease-in-out, transform 0.2s ease-in-out;
    }

    &--2 {
      left: 121px;
      top: 50px;
      transition: left 0.2s ease-in-out, top 0.2s ease-in-out, transform 0.2s ease-in-out;
    }

    &--3 {
      left: auto;
      right: 30px;
      top: 60px;
      transition: right 0.2s ease-in-out, top 0.2s ease-in-out, transform 0.2s ease-in-out;
    }

    &--4 {
      left: auto;
      right: 19px;
      bottom: 93px;
      transition: right 0.2s ease-in-out, bottom 0.2s ease-in-out, transform 0.2s ease-in-out;
    }

    &--5 {
      left: 225px;
      bottom: 23px;
      transition: left 0.2s ease-in-out, bottom 0.2s ease-in-out, transform 0.2s ease-in-out;
    }

    &--6 {
      left: 60px;
      bottom: 32px;
      transition: left 0.2s ease-in-out, bottom 0.2s ease-in-out, transform 0.2s ease-in-out;
    }
  }

  &__btn {
    position: absolute;
    z-index: 1;
    width: 140px;
    height: 140px;

    &:hover .svg-small-circle {
      stop-color: #f03a3a;
      transition: stop-color 0.2s ease-in-out;
    }

    &--1 {
      left: -30px;
      top: 135px;
      transition: left 0.2s ease-in-out, top 0.2s ease-in-out;

      &:hover {
        left: -60px;
        top: 125px;
        transition: left 0.2s ease-in-out, top 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--1 {
        left: 20px;
        top: 111px;
        transform: scale(1.1);
        transition: left 0.2s ease-in-out, top 0.2s ease-in-out, transform 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--2 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--3 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--4 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--5 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--6 {
        filter: grayscale(1);
      }
    }

    &--2 {
      left: 140px;
      top: -30px;
      transition: left 0.2s ease-in-out, top 0.2s ease-in-out;

      &:hover {
        left: 131px;
        top: -59px;
        transition: left 0.2s ease-in-out, top 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--2 {
        left: 112px;
        top: 21px;
        transform: scale(1.1);
        transition: left 0.2s ease-in-out, top 0.2s ease-in-out, transform 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--1 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--3 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--4 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--5 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--6 {
        filter: grayscale(1);
      }
    }

    &--3 {
      left: auto;
      right: 80px;
      top: 30px;
      transition: right 0.2s ease-in-out, top 0.2s ease-in-out;

      &:hover {
        right: 55px;
        top: 10px;
        transition: right 0.2s ease-in-out, top 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--3 {
        right: 5px;
        top: 35px;
        transform: scale(1.1);
        transition: right 0.2s ease-in-out, top 0.2s ease-in-out, transform 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--1 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--2 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--4 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--5 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--6 {
        filter: grayscale(1);
      }
    }

    &--4 {
      left: auto;
      right: 10px;
      bottom: 190px;
      transition: right 0.2s ease-in-out, bottom 0.2s ease-in-out;

      &:hover {
        right: -20px;
        bottom: 185px;
        transition: right 0.2s ease-in-out, bottom 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--4 {
        right: -15px;
        bottom: 83px;
        transform: scale(1.1);
        transition: right 0.2s ease-in-out, bottom 0.2s ease-in-out, transform 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--1 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--2 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--3 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--5 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--6 {
        filter: grayscale(1);
      }
    }

    &--5 {
      right: 190px;
      bottom: 15px;
      transition: right 0.2s ease-in-out, bottom 0.2s ease-in-out;

      &:hover {
        right: 180px;
        bottom: -15px;
        transition: right 0.2s ease-in-out, bottom 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--5 {
        left: 235px;
        bottom: -10px;
        transform: scale(1.1);
        transition: left 0.2s ease-in-out, bottom 0.2s ease-in-out, transform 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--1 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--2 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--3 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--4 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--6 {
        filter: grayscale(1);
      }
    }

    &--6 {
      left: 30px;
      bottom: 85px;
      transition: left 0.2s ease-in-out, bottom 0.2s ease-in-out;

      &:hover {
        left: 5px;
        bottom: 60px;
        transition: left 0.2s ease-in-out, bottom 0.2s ease-in-out;
      }

      // segment-6
      // left: 60px;
      // bottom: 32px;

      &:hover ~ .drum__segment--6 {
        left: 35px;
        bottom: 5px;
        transform: scale(1.1);
        transition: left 0.2s ease-in-out, bottom 0.2s ease-in-out, transform 0.2s ease-in-out;
      }

      &:hover ~ .drum__segment--1 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--2 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--3 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--4 {
        filter: grayscale(1);
      }

      &:hover ~ .drum__segment--5 {
        filter: grayscale(1);
      }
    }
  }

  &__image {
    filter: grayscale(1);
  }
}

.svg-small-circle {
  transition: stop-color 0.2s ease-in-out;
}

.svg {
  width: 221px;
  height: 221px;
  transition: right 0.2s ease-in-out;
}

.svg:hover {
  right: 20px;
  transition: right 0.2s ease-in-out;
}

.svg-color {
  transition: stop-color 0.2s ease-in-out;
}

.svg:hover .svg-color {
  stop-color: #f03a3a;
  transition: stop-color 0.2s ease-in-out;
}

