.modal {
  &__container {
    border-radius: 0;
    padding: 0;
    max-width: 785px;
    background-color: var(--science-blue);
  }

  &__fluid-container {
    border-radius: 0;
    padding: 0;
    max-width: 980px;
    background-color: var(--science-blue);
  }

  &__close {
    z-index: 100;

    &--feedback {
      right: 20px;
      top: 20px;

      svg {
        width: 25px;
        height: 25px;
        fill: var(--light-color);
        transition: opacity 0.2s ease-in-out;

        &:hover {
          opacity: 0.5;
          transition: opacity 0.2s ease-in-out;
        }
      }
    }

    &--case {
      right: 0;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 60px;
      height: 60px;
      background-color: rgb(255 255 255 / 60%);
      transition: background-color 0.2s ease-in-out;

      svg {
        width: 25px;
        height: 25px;
        fill: var(--tundora-color);
        transition: opacity 0.2s ease-in-out;
      }

      &:hover {
        background-color: rgb(255 255 255 / 100%);
        transition: background-color 0.2s ease-in-out;
      }
    }

    @media (max-width: 568px) {
      width: 40px;
      height: 40px;

      svg {
        width: 15px;
        height: 15px;
      }
    }
  }

  &__image {
    margin: 0;
  }
}
