.direction-card {
  position: relative;
  display: flex;
  justify-content: center;
  border: 1px solid rgb(255 255 255 / 60%);
  padding: 79px 30px 61px 45px;
  background-color: var(--torea-bay);
  transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  cursor: pointer;

  &::before {
    position: absolute;
    left: 50%;
    top: -30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--light-color);
    border-radius: 100%;
    width: 61.13px;
    height: 61.13px;
    font-family: "Noto", sans-serif;
    font-weight: 600;
    font-size: 28px;
    line-height: 1;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--torea-bay);
    transform: translateX(-50%);
    transition: display 0.2s ease-in-out;
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -17px;
    border-right: 1px solid rgb(255 255 255 / 60%);
    border-bottom: 1px solid rgb(255 255 255 / 60%);
    border-left: 1px solid rgb(255 255 255 / 60%);
    width: 90%;
    height: 17px;
    transform: translateX(-50%);
    transition: display 0.2s ease-in-out;
  }

  &__content {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 113px;
    background: url("../img/direction-card-icon.svg") left center / auto no-repeat;

    @media (max-width: 468px) {
      padding-left: 90px;
    }
  }

  &__title {
    margin-bottom: 5px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: calc(24px + 4 * (100vw / 1920));
    line-height: 1.18;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(24px + (4 + 4 * 0.7) * ((100vw - 320px) / 1920));
      text-transform: none;
    }
  }

  &__descr {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: calc(18px + 4 * (100vw / 1920));
    line-height: 1.18;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(18px + (4 + 4 * 0.7) * ((100vw - 320px) / 1920));
      text-transform: none;
    }
  }

  &:hover {
    box-shadow: 0 4px 40px rgb(0 0 0 / 25%);
    background-color: var(--royal-blue);
    transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

    &::after {
      display: none;
      transition: display 0.2s ease-in-out;
    }
  }

  @media (max-width: 568px) {
    padding: 20px;
    padding-top: 30px;
    padding-bottom: 20px;
  }
}
