/* reset btns default settings */
.swiper-button-prev,
.swiper-button-next {
  position: static;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.hero__btns {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80px;
  transform: rotate(180deg);

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 100%;
    width: 8px;
    height: 8px;
    background-color: var(--light-color);
    transform: translate(-50%, -50%);
  }
}

.hero-slider__btn {
  margin: 0;

  svg {
    display: block;
    width: 12.55px;
    height: 22px;
    fill: var(--light-color);
  }
}

.hero-slider__btn--next {
  svg {
    transform: rotate(180deg);
  }
}

/* scrollbar */
.hero-slider__scrollbar-wrapper {
  width: 100%;
}

.hero-slider__scrollbar {
  border-radius: 0;
  height: 6.5px;
  background: rgb(144 151 166 / 15%);

  &::before {
    content: "01";
    position: absolute;
    left: -50px;
    top: 50%;
    font-family: "Noto", sans-serif;
    font-weight: 600;
    font-size: 28px;
    line-height: 1;
    text-transform: uppercase;
    color: var(--light-color);
    transform: translateY(-50%);
  }

  &::after {
    content: "03";
    position: absolute;
    right: -50px;
    top: 50%;
    font-family: "Noto", sans-serif;
    font-weight: 600;
    font-size: 28px;
    line-height: 1;
    text-transform: uppercase;
    color: var(--light-color);
    transform: translateY(-50%);
  }
}

.swiper-scrollbar-drag {
  border-radius: 0;
  background-color: var(--light-color);
}
