.hero-slider {
  padding-top: 26px;
  width: 100%;

  &__wrapper {
    position: relative;
  }

  &__scrollbar-wrapper {
    display: block;

    @media (max-width: 768px) {
      display: none;
    }
  }

  &__item {
    display: grid;
    column-gap: 30px;
    padding-left: 5px;
    width: 100%;
    min-height: 600px;

    &--1 {
      grid-template-columns: 3.2fr 2fr;

      @media (max-width: 1024px) {
        grid-template-columns: 1fr;
      }
    }

    &--2 {
      grid-template-columns: 5fr 1fr;

      @media (max-width: 768px) {
        grid-template-columns: 1fr;
      }
    }

    &--3 {
      grid-template-columns: 2fr 1fr;

      @media (max-width: 768px) {
        grid-template-columns: 1fr;
      }
    }

    @media (max-width: 768px) {
      grid-template-rows: minmax(100px, 1fr);
      margin-bottom: 10px;
      padding-left: 0;
      min-height: 0;
      row-gap: 10px;
    }
  }

  &__num {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 0;
    display: block;
    font-family: "Noto Sans", sans-serif;
    font-weight: 900;
    font-style: normal;
    font-size: 360px;
    line-height: 1;
    -webkit-text-fill-color: transparent;
    background: linear-gradient(180deg, rgb(242 242 242 / 10%) 0%, rgb(255 255 255 / 0%) 100%);
    -webkit-background-clip: text;

    @media (max-width: 768px) {
      font-size: 200px;
    }
  }

  &__big-txt {
    display: block;
    grid-column: 1 / 1 span;

    @media (max-width: 768px) {
      margin-bottom: 0;
      width: 100%;
    }
  }

  &__btn-wrapper {
    z-index: 1;
    align-self: start;
    grid-column: 1 / 1 span;
  }

  &__main-btn {
    width: 420px;
    background-color: var(--pomegranate-color);

    &:hover {
      background-color: transparent;
    }

    @media (max-width: 768px) {
      align-self: center;
    }

    @media (max-width: 568px) {
      width: 99%;
    }
  }

  @media (max-width: 768px) {
    padding-top: 0;
  }
}

.swiper-pagination {
  bottom: -50px !important;
  display: none;

  @media (max-width: 768px) {
    display: block;
  }
}

.swiper-pagination-bullet-active {
  background-color: var(--light-color);
}
