/* lets-talk */
.lets-talk {
  padding: 160px 100px;
  background: url("../img/contact-bg.svg") bottom left / auto no-repeat, var(--science-blue);

  &__title {
    align-self: center;
    grid-column: 2 / span 5;
    margin-bottom: 19px;
    font-family: "Noto", sans-serif;
    font-weight: 700;
    font-size: 75px;
    line-height: 1.13;
    text-transform: uppercase;
  }

  &__descr {
    margin-bottom: 38px;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: calc(17px + 5 * (100vw / 1920));
    line-height: 26px;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(17px + (5 + 5 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  @media (max-width: 768px) {
    padding: 50px 20px;
  }
}

/* lets-talk-form */
.lets-talk-form {
  column-gap: 20px;

  .form__label {
    @media (max-width: 1024px) {
      grid-column: 1 / span 12 !important;
    }
  }

  .form__label:nth-child(1) {
    grid-column: 1 / span 6;
  }

  .form__label:nth-child(2) {
    grid-column: 7 / span 6;
  }

  .form__label:nth-child(3) {
    grid-column: 1 / span 6;
    grid-row: 2 / 1 span;
  }

  .form__label:nth-child(4) {
    grid-column: 7 / span 6;
    grid-row: 2 / 1 span;
  }

  .form__btn {
    &--send {
      grid-column: 1 / span 4;
      margin: 0;
    }

    &--file {
      grid-column: 5 / span 7;
    }
  }
}
