/* ty-modal */
.ty-modal {
  &__content {
    position: relative;

    // display: none;
    display: flex;
    flex-flow: column wrap;
    align-items: flex-start;
    padding: 137px 107px;

    // opacity: 0;
    // visibility: hidden;
    // transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    // &--visible {
    //   display: flex;
    //   opacity: 1;
    //   visibility: visible;
    //   transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    // }
    background: url("../img/contact-bg.svg") left center / auto no-repeat;

    // background: url("../img/insol-log-bg-small.svg") left center / auto no-repeat;
  }

  &__list {
    display: grid;
    column-gap: 32px;
    grid-template-columns: repeat(3, 1fr);
  }

  &__svg {
    width: 64px;
    height: 64px;
    fill: var(--pink-color);
    transition: fill 0.3s ease-in-out;

    &:hover {
      fill: var(--light-gray);
      transition: fill 0.3s ease-in-out;
    }
  }
}
