.feedback {
  padding: 120px 0;
  background-image: url("../img/map-bg.jpg");
  background-position: center top;
  background-size: 100% auto;
  background-repeat: no-repeat;

  &__container {
    display: grid;
    column-gap: 50px;
    grid-template-columns: 2fr 1fr;
    padding-top: 189px;

    @media (max-width: 1024px) {
      grid-template-columns: 1fr;
    }

    @media (max-width: 768px) {
      padding-top: 0;
    }
  }

  &__list {
    align-self: end;
    margin-bottom: 70px;

    @media (max-width: 1024px) {
      display: none;
      margin-bottom: 0;
    }
  }

  &__item {
    display: flex;
    align-items: center;
  }

  &__item:not(:last-child) {
    margin-bottom: 48px;
  }

  &__link {
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-size: 22px;
    line-height: 1.36;
    color: var(--black-color);
  }

  &__svg {
    margin-right: 27px;
    fill: var(--science-blue);
  }

  &__item:nth-child(1) {
    svg {
      width: 19px;
      height: 27px;
    }
  }

  &__item:nth-child(2) {
    svg {
      width: 17px;
      height: 17px;
    }
  }

  &__item:nth-child(3) {
    svg {
      margin-right: 22px;
      width: 26.5px;
      height: 21.5px;
    }
  }

  @media (max-width: 768px) {
    padding: 50px 0;
  }
}
