/* form */
.form {
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(12, 1fr);
  row-gap: 14.5px;

  &__label {
    grid-column: 1 / span 12;

    @media (max-width: 1024px) {
      grid-column: 1 / span 12;
    }
  }

  &__elem {
    border: 1px solid rgb(255 255 255 / 30%);
    border-radius: 10px;
    padding: 13px 21.55px;
    width: 100%;
    font-size: calc(10px + 10 * (100vw / 1920));
    line-height: 1.2;
    color: var(--light-color);
    background: var(--torea-bay);

    &--cases {
      border: 1px solid rgb(255 255 255 / 30%);
      border-radius: 10px;
      color: var(--light-color);
      background: rgb(13 51 151 / 20%);

      &::placeholder {
        color: rgb(255 255 255 / 60%);
      }

      &:focus {
        border-color: var(--science-blue);
      }
    }

    @media (max-width: 767px) {
      font-size: calc(10px + (10 + 10 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    align-self: start;
    border: 1px solid var(--light-color);
    border-radius: 10px;
    height: 60px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: var(--dark-color);
    background-color: var(--light-color);
    cursor: pointer;

    svg {
      display: block;
      margin-right: 14px;
      width: 24.5px;
      height: 31px;
      fill: var(--light-color);
    }

    &:hover {
      background-color: var(--mercury-color);
      transition: background-color 0.2s ease-in-out;
    }

    &--cases {
      border: 1px solid var(--science-blue);
      color: var(--light-color);
      background-color: var(--science-blue);
      transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border 0.2s ease-in-out;

      &:hover {
        border: 1px solid var(--science-blue);
        color: var(--science-blue);
        background-color: var(--light-color);
        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border 0.2s ease-in-out;
      }
    }

    &--send {
      display: flex;
      justify-content: center;
      grid-column: 1 / span 5;
      width: 182px;

      @media (max-width: 1024px) {
        grid-column: 1 / span 12 !important;
        width: 100%;
        height: 40px;
      }

      @media (max-width: 768px) {
        justify-content: center;
        grid-column: 1 / span 12;
        grid-row: 6 / span 1;
        width: 100%;
      }
    }

    &--file {
      grid-column: 6 / span 6;
      border: 1px var(--light-color) solid;
      padding-right: 62px;
      padding-left: 19px;
      width: 280px;
      font-family: "Roboto", sans-serif;
      font-weight: 700;
      font-size: calc(10px + 8 * (100vw / 1920));
      line-height: 1;
      color: var(--light-color);
      background-color: var(--torea-bay);
      transition: background-color 0.2s ease-in-out;

      svg {
        display: block;
        margin-right: 14px;
        width: 24.5px;
        height: 31px;
        fill: var(--light-color);

        @media (max-width: 768px) {
          width: 18px;
          height: 22px;
          font-weight: 300;
        }
      }

      &:hover {
        background-color: var(--torea-bay);
        transition: background-color 0.2s ease-in-out;
      }

      @media (max-width: 1024px) {
        grid-column: 1 / span 12 !important;
        width: 100%;
        height: 40px;
      }

      @media (max-width: 768px) {
        grid-column: 1 / span 12;
        grid-row: 5 / span 1;
        width: 100%;
      }

      @media (max-width: 767px) {
        font-size: calc(10px + (8 + 8 * 0.7) * ((100vw - 320px) / 1920));
      }
    }
  }

  &__note {
    font-family: "RobotoCondensed", sans-serif;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: rgb(255 255 255 / 60%);
  }
}

/* textarea */
.textarea {
  height: 138px;
}
