.hero-slider-note {
  align-self: end;
  grid-column: 2 / span 1;
  grid-row: 1 / span 1;
  margin-bottom: 80px;

  &__svg {
    margin-bottom: 10px;
    width: 69px;
    height: 69px;
    fill: var(--light-color);

    @media (max-width: 1024px) {
      width: 49px;
      height: 49px;
    }
  }

  &__txt {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 22px;
    line-height: 1.2;
    color: var(--light-color);

    p {
      margin: 0;
    }

    @media (max-width: 1024px) {
      font-size: 15px;
    }
  }

  @media (max-width: 1024px) {
    align-self: start;
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    margin-bottom: 0;
  }
}
