.section-title {
  font-family: "Noto", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: calc(34px + 12 * (100vw / 1920));
  line-height: 1;
  color: var(--light-color);

  @media (max-width: 767px) {
    font-size: calc(34px + (12 + 12 * 0.7) * ((100vw - 320px) / 1920));
  }
}

.section-group-1 {
  background: url("../img/s-g-1.jpg") center / cover no-repeat;
}

.section-group-2 {
  background: url("../img/s-g-2.png") center / cover no-repeat;
}

.section-group-3 {
  background: linear-gradient(7.85deg, #3f346e 7.28%, #f03a3a 34.12%, #0b327d 92.49%);
}

.section-group-4 {
  background: url("../img/net-bg.png") center 71.5% / cover no-repeat;
}
