.arrow-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 43px;
  height: 43px;
  background-color: var(--light-color);
  transition: background-color 0.3s ease-in-out;

  &__svg {
    margin-top: 5px;
    width: 16px;
    height: 25px;
    fill: var(--pomegranate-color);
    transform: rotate(-90deg);
    transition: fill 0.3s ease-in-out;

    @media (max-width: 568px) {
      width: 8.5px;
      height: 14.5px;
    }

    @media (max-width: 1024px) {
      order: 0;
    }
  }

  @media (max-width: 568px) {
    width: 24px;
    height: 24px;
  }
}
