.case-modal-content {
  margin: 0;
  padding: 0 100px;
  padding-top: 50px;
  padding-bottom: 88px;
  height: 100%;
  background: rgb(1 76 221 / 60%);

  &__title {
    margin-bottom: 10px;
    font-family: "Noto", sans-serif;
    font-weight: 600;
    font-size: calc(34px + 16 * (100vw / 1920));
    line-height: 1.2;
    text-transform: uppercase;
    color: var(--light-color);

    &--small {
      margin-bottom: 38px;
      font-weight: 400;
      font-size: calc(24px + 18 * (100vw / 1920)) !important;
      line-height: 1.2;
      text-transform: none;

      @media (max-width: 767px) {
        font-size: calc(24px + (18 + 18 * 0.7) * ((100vw - 320px) / 1920)) !important;
      }
    }

    @media (max-width: 767px) {
      font-size: calc(34px + (16 + 16 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__descr-title {
    margin-bottom: 14px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 1;
    color: var(--light-color);

    span {
      display: flex;
      align-items: center;
      padding-left: 14px;
      width: 180px;
      height: 33px;
    }

    &--problem span {
      background-color: var(--pomegranate-color);
    }

    &--decision span {
      border: 1px solid var(--light-color);
      background-color: var(--catalina-blue);
    }
  }

  &__descr {
    margin-bottom: 15px;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-size: calc(12px + 6 * (100vw / 1920));
    line-height: 1.2;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(12px + (6 + 6 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__info {
    margin-bottom: 48px;
  }

  &__elem:not(:last-child) {
    margin-bottom: 32px;
  }

  &__list {
    margin-bottom: 35px;
    row-gap: 20px;
  }

  &__item {
    position: relative;
    padding-left: 20px;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-size: calc(16px + 2 * (100vw / 1920));
    line-height: 21px;
    color: var(--light-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
    }

    &:not(:last-child) {
      margin-bottom: 5px;
    }

    @media (max-width: 767px) {
      font-size: calc(16px + (2 + 2 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  .form__btn--send {
    grid-column: 1 / span 3;

    @media (max-width: 1024px) {
      height: 40px;
    }
  }

  .form__btn--file {
    grid-column: 4 / span 4;
  }

  .form__note {
    grid-column: 8 / span 5;
  }

  &--id-case {
    padding: 0 50px;
    padding-top: 50px;
    padding-bottom: 88px;
  }

  @media (max-width: 768px) {
    padding: 0 20px;
    padding-top: 30px;
    padding-bottom: 58px;
  }
}
