.steps {
  padding-top: 122px;
  padding-bottom: 100px;

  &__list {
    counter-reset: my-awesome-counter;
    display: grid;
    column-gap: 37px;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 43px;

    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 468px) {
      grid-template-columns: 1fr;
      row-gap: 60px;
    }
  }

  &__item {
    counter-increment: my-awesome-counter;
    position: relative;
    padding-top: 183px;

    &::before {
      content: "0"counter(my-awesome-counter);
      position: absolute;
      left: 66px;
      top: 122px;
      z-index: 2;
      font-family: "Montserrat", sans-serif;
      font-weight: 400;
      font-style: normal;
      font-size: 60px;
      line-height: 30px;
      text-align: center;
      color: var(--light-color);
      transform: translateX(-50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 10px;
      width: 131px;
      height: 131px;
      background-position: center;
      background-size: auto;
      background-repeat: no-repeat;
      background-color: var(--science-blue);
    }

    &:nth-child(1) {
      &::after {
        background-image: url("../img/steps-1.svg");
      }
    }

    &:nth-child(2) {
      &::after {
        background-image: url("../img/steps-2.svg");
      }
    }

    &:nth-child(3) {
      &::after {
        background-image: url("../img/steps-3.svg");
      }
    }

    &:nth-child(4) {
      &::after {
        background-image: url("../img/steps-4.svg");
      }

      .steps-item__title {
        width: 45%;
      }
    }

    &:nth-child(5) {
      &::after {
        background-image: url("../img/steps-5.svg");
      }
    }

    &:nth-child(6) {
      &::after {
        background-image: url("../img/steps-6.svg");
      }

      .steps-item__title {
        width: 45%;
      }
    }
  }

  &__title {
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.18;
    color: var(--light-color);
  }

  @media (max-width: 768px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

/* steps-item */
.steps-item {
  &__title {
    display: block;
    width: 31%;
    min-height: 65px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.18;
    color: var(--light-color);

    @media (max-width: 468px) {
      margin-bottom: 10px;
      min-height: 0;
    }
  }

  &__descr {
    width: 85%;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 22px;
    line-height: 1.18;
    color: var(--light-color);

    @media (max-width: 1024px) {
      width: 100%;
    }
  }

  &:nth-child(4) {

  }
}
