.co-worker-card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  overflow: hidden;
  padding: 22px 28px;
  height: 380px;

  &__name {
    display: block;
    margin-bottom: 16px;
    font-family: "Noto", sans-serif;
    font-weight: 500;
    font-size: 30px;
    line-height: 1;
    color: var(--light-color);

    @media (max-width: 568px) {
      font-size: 24px;
    }
  }

  &__descr {
    display: block;
    margin-bottom: 5px;
    height: 0;
    font-family: "Noto", sans-serif;
    font-weight: 300;
    font-style: normal;
    font-size: 16px;
    line-height: 1.37;
    color: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, height 0.2s ease-in-out;

    @media (max-width: 568px) {
      font-size: 14px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 118px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
    row-gap: 16px;
  }

  &__svg {
    fill: var(--manatee-color);

    &:nth-child(1) {
      width: 30px;
      height: 30px;
    }

    @media (max-width: 568px) {
      width: 20px;
      height: 20px;
    }
  }

  &:hover {
    justify-content: space-between;
  }

  &:hover &__list {
    opacity: 1;
    visibility: visible;
    transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
  }

  &:hover &__descr {
    display: block;
    height: 120px;
    opacity: 1;
    visibility: visible;
    transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, height 0.2s ease-in-out;
  }
}
