.achieves {
  padding-top: 108px;
  padding-bottom: 112px;
  box-shadow: 0 0 100px rgb(0 0 0 / 15%);
  background-color: var(--science-blue);
  backdrop-filter: blur(2px);

  &__list {
    display: grid;
    column-gap: 50px;
    grid-template-columns: repeat(4, 1fr);

    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 30px;
    }

    @media (max-width: 568px) {
      grid-template-columns: 1fr;
      row-gap: 30px;
    }
  }

  &__descr {
    display: block;
    width: 65%;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.35;
    color: var(--light-color);

    @media (max-width: 768px) {
      font-size: 16px;
    }
  }

  @media (max-width: 1024px) {
    padding: 20px 0;
  }
}

/* achieves-title */
.achieves-title {
  display: block;
  margin-bottom: 7px;
  font-family: "Noto", sans-serif;
  color: var(--light-color);

  &__big-txt {
    font-weight: 900;
    font-size: 80px;
    line-height: 1;

    @media (max-width: 768px) {
      font-size: 50px;
    }
  }

  &__small-txt {
    font-weight: 900;
    font-size: 25px;
    line-height: 1.36;
  }
}
