.footer {
  margin-top: -62px;
  padding-top: 113px;
  padding-bottom: 66px;
  background: url("../img/footer-bg.jpg") center / cover no-repeat;

  &__container {
    row-gap: 25px;
  }

  &__logo {
    grid-column: 1 / span 3;
    grid-row: 2 / span 2;

    @media (max-width: 1024px) {
      grid-column: 1 / 6 span;
      grid-row: 2 / span 1;
    }

    @media (max-width: 768px) {
      width: 90px;
      height: 25px;
    }

    @media (max-width: 600px) {
      justify-self: start;
      grid-column: 1 / 4 span;
    }
  }

  &__company {
    grid-column: 5 / span 4;
    font-family: "Noto", sans-serif;
    font-weight: 700;
    font-size: calc(20px + 10 * (100vw / 1920));
    line-height: 1;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(20px + (10 + 10 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 1024px) {
      grid-column: 1 / 6 span;
      grid-row: 3 / span 1;
    }

    @media (max-width: 600px) {
      justify-self: start;
      grid-column: 1 / 4 span;
    }
  }

  &__address {
    grid-column: 5 / span 4;
    grid-row: 2 / span 1;
    font-family: "RobotoCondensed", sans-serif;
    font-weight: 300;
    font-size: calc(20px + 2 * (100vw / 1920));
    line-height: 26px;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(20px + (2 + 2 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 1024px) {
      justify-self: start;
      grid-column: 1 / 6 span;
      grid-row: 6 / span 1;
    }

    @media (max-width: 600px) {
      justify-self: start;
      grid-column: 1 / 4 span;
    }
  }

  &__social-media {
    grid-column: 5 / span 4;
    grid-row: 3 / span 1;

    @media (max-width: 1024px) {
      justify-self: start;
      grid-column: 1 / 6 span;
      grid-row: 7 / span 1;
    }

    @media (max-width: 600px) {
      justify-self: start;
      grid-column: 1 / 4 span;
    }
  }

  &__privacy-policy {
    grid-column: 5 / span 2;
    grid-row: 4 / span 1;
    width: 50%;
    font-family: "RobotoCondensed", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 2.5;
    color: var(--light-color);
    opacity: 0.4;

    @media (max-width: 1024px) {
      justify-self: start;
      grid-column: 1 / 6 span;
      grid-row: 8 / span 1;
      width: 100%;
    }

    @media (max-width: 600px) {
      justify-self: start;
      grid-column: 1 / 4 span;
    }
  }

  &__tel {
    justify-self: end;
    grid-column: 9 / 4 span;
    font-family: "Noto", sans-serif;
    font-weight: 700;
    font-size: calc(20px + 20 * (100vw / 1920));
    line-height: 1;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(20px + (20 + 20 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 1024px) {
      justify-self: start;
      grid-column: 1 / 6 span;
      grid-row: 4 / span 1;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__mail {
    justify-self: end;
    grid-column: 9 / 4 span;
    grid-row: 2 / span 1;
    font-size: calc(20px + 22 * (100vw / 1920));

    @media (max-width: 767px) {
      font-size: calc(20px + (22 + 22 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 1024px) {
      justify-self: start;
      grid-column: 1 / 6 span;
      grid-row: 5 / span 1;
    }

    @media (max-width: 600px) {
      justify-self: start;
      grid-column: 1 / 4 span;
    }
  }

  &__nav {
    justify-self: end;
    grid-column: 9 / 4 span;
    grid-row: 4 / span 1;

    @media (max-width: 1024px) {
      justify-self: start;
      grid-column: 1 / 5 span;
      grid-row: 1 / span 1;
    }

    @media (max-width: 600px) {
      justify-self: start;
      grid-column: 1 / 4 span;
      grid-row: 1 / span 1;
      width: 100%;
    }
  }

  &__elem {
    @media (max-width: 1024px) {
      font-size: 20px;
    }
  }

  &--cases {
    margin-top: 0;
    padding-top: 178px;

    @media (max-width: 1200px) {
      margin-top: -100px;
    }
  }

  &--contacts {
    margin-top: 0;
    padding-top: 50px;
  }
}

/* footer-social-media */
.footer-social-media {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 97px;
  row-gap: 14px;

  &__link {
    display: block;
    width: auto;
  }

  &__svg {
    width: 22px;
    height: 22px;
    fill: var(--manatee-color);
  }
}
