.team {
  padding: 263px 0;

  &__logo {
    display: block;
    grid-column: 1 / span 1;
    grid-row: 1 / span 2;
    margin-right: 73px;
    margin-bottom: 28px;
    width: 161px;
    height: 200px;

    @media (max-width: 568px) {
      margin-bottom: 50px;
      width: 121px;
      height: 150px;
    }
  }

  &__title {
    grid-column: 2 / span 1;
    margin-bottom: 20px;
    font-family: "Noto", sans-serif;
    font-weight: 500;
    font-size: calc(34px + 26 * (100vw / 1920));
    line-height: 50px;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(34px + (26 + 26 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__descr {
    grid-column: 2 / span 1;
    grid-row: 2 / span 1;
    width: 62%;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-size: calc(18px + 4 * (100vw / 1920));
    line-height: 1.36;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(18px + (4 + 4 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 768px) {
      width: 100%;
    }
  }

  &__list {
    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 20px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 688px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    &:nth-child(2) {
      background: radial-gradient(111.75% 95.66% at 75.66% 24.87%, rgb(21 21 21 / 0%) 14.06%, #3f346e 57.29%), url("../img/hill.jpg") right top / contain no-repeat;
      background-blend-mode: normal;
    }

    &:nth-child(3) {
      background: radial-gradient(111.75% 95.66% at 75.66% 24.87%, rgb(21 21 21 / 0%) 14.06%, #3f346e 57.29%), url("../img/lunev.jpg") right top / contain no-repeat;
      background-blend-mode: normal;
    }

    &:nth-child(4) {
      background: radial-gradient(111.75% 95.66% at 75.66% 24.87%, rgb(21 21 21 / 0%) 14.06%, #3f346e 57.29%), url("../img/kleiman-2.jpg") right top / contain no-repeat;
      background-blend-mode: normal;
    }

    &:nth-child(5) {
      background: radial-gradient(111.75% 95.66% at 75.66% 24.87%, rgb(21 21 21 / 0%) 14.06%, #3f346e 57.29%), url("../img/rudnykh.jpg") right top / contain no-repeat;
      background-blend-mode: normal;
    }
  }

  &-item--first {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: 1 / span 2;
    padding-right: 54px;
    padding-left: 54px;
    background: url("../img/team-item-bg.png") center / cover no-repeat;

    @media (max-width: 1350px) {
      min-height: 393px;
    }

    @media (max-width: 1024px) {
      grid-column: 1 / span 2;
    }

    @media (max-width: 568px) {
      flex-flow: column wrap;
      align-items: start;
      padding: 30px;
    }

    @media (max-width: 688px) {
      grid-column: 1 / span 1;
    }
  }

  @media (max-width: 1024px) {
    padding: 100px 0;
  }
}
