.scheme {
  padding-top: 260px;
  padding-bottom: 105px;

  &__container {
    position: relative;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
  }

  &__title {
    font-family: "Noto", sans-serif;
    font-weight: 600;
    font-style: normal;
    font-size: calc(34px + 11 * (100vw / 1920));
    line-height: 1.44;
    text-align: center;
    text-transform: uppercase;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(34px + (11 + 11 * 0.7) * ((100vw - 320px) / 1920));
      text-transform: none;
    }

    @media (max-width: 768px) {
      position: static;
      right: 0;
    }
  }

  &__img {
    display: block;
    padding-top: 35px;
  }

  @media (max-width: 768px) {
    padding-top: 50px !important;
  }

  @media (max-width: 568px) {
    padding-top: 50px;
  }
}

.fluid-pt {
  padding-top: 650px;
}
