/* .rdy-solutions-list */
.rdy-solutions-list {
  z-index: 10;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);

  &--cases {
    // position: absolute;
  }

  &--id-case {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    height: 100%;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, auto);
    }
  }

  @media (max-width: 1024px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 768px) {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* rdy-solutions-list-item */
.rdy-solutions-list-item {
  position: relative;
  display: flex;
  flex-flow: column wrap;
  justify-content: end;
  border: 1px solid var(--light-color);
  padding: 42px 30px 42px 38px;
  min-height: 393px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__descr {
    display: block;
    margin-bottom: 20px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: calc(10px + 14 * (100vw / 1920));
    line-height: 1.2;
    color: var(--light-color);

    @media (max-width: 767px) {
      font-size: calc(10px + (14 + 14 * 0.7) * ((100vw - 320px) / 1920));
    }
  }

  &__link {
    display: flex;
    align-items: center;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: calc(10px + 12 * (100vw / 1920));
    line-height: 1.2;
    text-decoration: underline;
    color: var(--light-color);

    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }

    &--back {
      span {
        transform: rotate(180deg);
      }
    }

    @media (max-width: 767px) {
      font-size: calc(10px + (12 + 12 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 568px) {
      font-size: 12px;
    }
  }

  &__svg-wrapper {
    margin-right: 20px;
  }

  &__svg {
    margin-top: 0 !important;
    transform: rotate(180deg) !important;
  }

  &:not(:last-child) {
    min-height: 393px;

    @media (max-width: 768px) {
      min-height: 160px;
    }
  }

  &:nth-child(1) {
    background: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, #000 66.67%), url("../img/rdy-sol-1.jpg") center / cover no-repeat;
    background-blend-mode: normal;
  }

  &:nth-child(2) {
    background: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, #000 66.67%), url("../img/rdy-sol-2.png") center / cover no-repeat;
    background-blend-mode: normal;
  }

  &:nth-child(3) {
    background: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, #000 66.67%), url("../img/rdy-sol-3.jpg") center / cover no-repeat;
    background-blend-mode: normal;
  }

  &:nth-child(4) {
    background: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, #000 66.67%), url("../img/rdy-sol-4.jpg") center / cover no-repeat;
    background-blend-mode: normal;
  }

  &:nth-child(5) {
    grid-column: 2 / span 2;
    background: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, #000 66.67%), url("../img/rdy-sol-fluid.jpg") center / cover no-repeat;
    background-blend-mode: normal;

    @media (max-width: 1024px) {
      align-items: center;
      justify-content: center;
      grid-column: 1 / span 2;
      padding: 20px 0;
      height: auto !important;
      min-height: 0;
      background: none;
    }
  }

  &--fluid {
    @media (max-width: 1024px) {
      align-items: center;
      justify-content: center;
      grid-column: 1 / span 2;
      padding: 20px 0;
      height: auto !important;
      min-height: 0;
      background: none !important;
    }
  }

  &--fluid .rdy-solutions-list-item__svg-wrapper {
    @media (max-width: 568px) {
      width: 24px;
      height: 24px;
    }
  }

  &--fluid .rdy-solutions-list-item__svg-wrapper--back {
    @media (max-width: 1024px) {
      order: 0;
      margin-right: 17px;
    }
  }

  &--fluid .rdy-solutions-list-item__txt--back {
    @media (max-width: 1024px) {
      order: 1;
    }
  }

  &--fluid .rdy-solutions-list-item__svg {
    @media (max-width: 568px) {
      width: 8.5px;
      height: 14.5px;
    }
  }

  &--fluid .rdy-solutions-list-item__link {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: calc(20px + 12 * (100vw / 1920));
    line-height: 1.2;
    text-decoration: none;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    @media (max-width: 767px) {
      font-size: calc(20px + (12 + 12 * 0.7) * ((100vw - 320px) / 1920));
    }

    @media (max-width: 568px) {
      font-size: 16px;
    }
  }

  &--fluid .rdy-solutions-list-item__link:hover {
    color: var(--pomegranate-color);
    transition: color 0.3s ease-in-out;
  }

  &--fluid .rdy-solutions-list-item__link:hover .rdy-solutions-list-item__svg-wrapper {
    background-color: var(--pomegranate-color);
    transition: background-color 0.3s ease-in-out;
  }

  &--fluid .rdy-solutions-list-item__link:hover .rdy-solutions-list-item__svg {
    fill: var(--light-color);
    transition: fill 0.3s ease-in-out;
  }

  @media (max-width: 1080px) {
    padding: 30px;
  }

  @media (max-width: 1024px) {
    grid-template-rows: repeat(2, auto);
    padding: 20px 20px 20px 15px;
    min-height: 0;
  }

  // @media (max-width: 568px) {
  //   padding: 0;
  //   padding-right: 20px;
  //   padding-bottom: 20px;
  //   padding-left: 15px;
  //   height: 160px;
  //   min-height: 160px;
  // }
}
