.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
  padding: 33px 0;

  &__mail {
    display: inline-block;

    @media (max-width: 1024px) {
      margin-bottom: 30px;
      font-size: 20px;
    }
  }

  &__social-media {
    display: none;

    @media (max-width: 1024px) {
      display: grid;
    }
  }

  &__container {
    align-items: center;
  }

  &__logo {
    z-index: 100;
    grid-column: 1 / span 2;

    @media (max-width: 768px) {
      width: 90px;
      height: 25px;
    }
  }

  &__nav {
    justify-self: end;

    @media (max-width: 1024px) {
      display: flex;
      flex-flow: column wrap;
      margin-bottom: 20px;
      font-size: 40px;
    }
  }

  &__nav .nav__list {
    @media (max-width: 1024px) {
      display: flex;
      flex-flow: column wrap;
      align-items: center;
      font-size: 20px;
    }
  }

  &__nav .nav__item {
    @media (max-width: 1024px) {
      margin-right: 0;
      margin-bottom: 20px;
      font-size: 20px;
    }
  }

  &__nav .nav__link {
    @media (max-width: 1024px) {
      font-size: 20px;
    }
  }

  &__right-side {
    display: flex;
    justify-content: space-between;
    grid-column: 4 / span 9;

    @media (max-width: 1024px) {
      position: absolute;
      display: flex;
      flex-flow: column wrap;
      align-items: center;
      justify-content: center;
      height: 100vh;
      background-color: var(--catalina-blue);
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
      inset: 0;
    }
  }

  &--contacts {
    position: static;
    padding: 30px 0;
    background: linear-gradient(16.26deg, #3f346e -92.19%, #f03a3a -35.53%, #0b327d 87.65%);
  }

  @media (max-width: 768px) {
    padding: 22.5px 0;
  }

  @media (max-width: 1024px) {
    position: static;
    background: linear-gradient(16.26deg, #3f346e -92.19%, #f03a3a -35.53%, #0b327d 87.65%);
  }

  @media (max-width: 768px) {
    position: static;
    background: linear-gradient(16.26deg, #3f346e -92.19%, #f03a3a -35.53%, #0b327d 87.65%);
  }
}

.menu--active {
  z-index: 50;
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
